.row-wrapper {
  width: 100%;
  background-size: cover;
  background-position: center;
}

.row {
  width: 100%;
  clear: both;
  overflow: hidden;
}

.row-liquid {
  width: 100%;
}

.row-liquid {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

@media screen and (max-width: 800px) {
  .row-layout {
    padding-left: 0px;
    padding-right: 0px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }
}

@media screen and (min-width: 800px) and (max-width: 1200px) {
  .row-layout {
    padding-left: 16px;
    padding-right: 16px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }
}

@media screen and (min-width: 1200px) and (max-width: 1640px) {
  .row-layout {
    padding-left: 32px;
    padding-right: 32px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }
}

@media screen and (min-width: 1640px) {
  .row {
    width: 1600px;
    margin: 0 auto;
  }
  .row-liquid {
    width: 100%;
  }
}



